






import {Component, Vue} from 'vue-property-decorator';

const dateFormat = require('dateformat');
const sha256 = require('sha256')
import WebSdkExampleType, {WebSdkExampleCreatePaymentArgType, WebSdkExampleSCardType, WebSdkExampleScFieldsType, WebSdkPaymentResultType} from '@/struct/WebSdkExampleType';
import WebSdkOpenPageParams from "@/struct/WebSdkOpenPageParams";

@Component({
    components: {}
})
export default class SafeChargeWebSdk extends Vue {

    private RequestParams!: WebSdkOpenPageParams
    private SafeChargeExample?: WebSdkExampleType
    private SafeChargeSCard?: WebSdkExampleSCardType


    private created(): void {
        this.RequestParams = (this.$parent as any).RequestParams
    }

    private mounted(): void {
        (window as any).DoSubmit = this.OnSubmit
        this.InitWebSdk()
    }

    private InitWebSdk(): void {
        if (('SafeCharge' in window) == false) {
            setTimeout(() => this.InitWebSdk(), 300)
            return
        }

        // @ts-ignore
        // eslint-disable-next-line
        this.SafeChargeExample = SafeCharge({
            env: 'test',
            merchantId: this.RequestParams.merchantId,
            merchantSiteId: this.RequestParams.merchantSiteId,
        });

        const ScFields = this.SafeChargeExample?.fields({
            locale: 'en'
        })

        const style = {
            base: {
                fontFamily: 'Roboto, sans-serif',
                color: "#23262C",
                fontSmoothing: 'antialiased',
                '::placeholder': {
                    color: '#CECFD0'
                }
            },
            invalid: {
                color: '#FF6231',
                ':focus': {
                    color: '#FF6231'
                },
                '::placeholder': {
                    color: '#FF6231'
                }
            },
            empty: {
                color: '#CECFD0',
                '::placeholder': {
                    color: '#CECFD0'
                }
            },
            valid: {
                color: '#23262C'
            }
        };

        if (window.innerWidth > 600) {
            (style.base as any)["fontSize"] = "16pt";
            (style.base as any)["::placeholder"]["fontSize"] = "16pt";
        } else if (window.innerWidth > 450) {
            (style.base as any)["fontSize"] = "12pt";
            (style.base as any)["::placeholder"]["fontSize"] = "12pt";
        }

        this.SafeChargeSCard = ScFields?.create('card', {style: style});
        this.SafeChargeSCard?.attach(document.getElementById('safe-charge-web-sdk-iframe'));
    }

    private OnSubmit(cardHolderName: string): void {
        console.log('OnSubmit')
        try {
            const paymentInfo: WebSdkExampleCreatePaymentArgType = {
                clientUniqueId: this.RequestParams.clientUniqueId ?? "",
                sessionToken: this.RequestParams.sessionToken ?? "",
                currency: this.RequestParams.currency ?? "",
                amount: String(Number(this.RequestParams.amount)),
                cardHolderName: cardHolderName,
                paymentOption: this.SafeChargeSCard
            }

            if ("email" in this.RequestParams && "country" in this.RequestParams) {
                paymentInfo.billingAddress = {
                    country: this.RequestParams.country ?? "",
                    email: this.RequestParams.email ?? ""
                }
            }

            console.log(paymentInfo)
            this.SafeChargeExample?.createPayment(paymentInfo, (result: WebSdkPaymentResultType) => {
                console.log(result)
                if (result.result == "ERROR") {
                    this.$emit('error', result.errorDescription)
                } else if (result.result == "DECLINED") {
                    this.$emit('error', "Sorry, your payment was rejected. Please try again.")
                } else {
                    location.href = this.RequestParams.return + "?success_payment"
                }
                this.$emit('finishPayment', result)
            })
        } catch (e) {
            console.log(e)
            this.$emit('finishPayment', undefined)
        }
    }
}
