






































































































import {Component, Vue} from 'vue-property-decorator';

import 'reset-css';

import "@fortawesome/fontawesome-free/less/fontawesome.less"
import "@/assets/style/fonts.less"
import "@/assets/style/common.less"

import "@/assets/style/buefy.custom.min.css"
import SafeChargeWebSdkForm from "@/components/SafeChargeWebSdkForm.vue";
import {WebSdkPaymentResultType} from "@/struct/WebSdkExampleType";
import WebSdkOpenPageParams from "@/struct/WebSdkOpenPageParams";
import sha256 from "sha256";

@Component({
    components: {SafeChargeWebSdkForm}
})
export default class Home extends Vue {
    private AcceptedTerns: boolean = false
    private CardHolderName: string = ""

    private ErrorCardText?: string
    private ErrorAcceptedTerns: boolean = false
    private ErrorCardHolderName: boolean = false

    private HasSubmitted: boolean = false

    private RequireParams!: string[]
    private RequestParams?: WebSdkOpenPageParams

    private created(): void {
        this.AcceptedTerns = false
        this.CardHolderName = ""
        this.HasSubmitted = false

        this.ErrorAcceptedTerns = false
        this.ErrorCardHolderName = false
        this.ErrorCardText = undefined

        this.InitParams()
    }

    private mounted() {
        //
    }

    private InitParams(): void {
        // if (location.hostname.startsWith("localhost") || location.hostname.startsWith("127.0.0.1")) {
        //     this.RequestParams = {
        //         country: "UK",
        //         email: "lod.skot@gmail.com",
        //         amount: "10",
        //         bundle: "12Gb",
        //         currency: "USD",
        //         sessionToken: "794b3cad-b10f-4252-9b0a-599f26cc9ba7",
        //         clientUniqueId: "dd593d5d-c2ce-4c17-8530-e91fbd80f1a9",
        //         merchantSiteId: "205186",
        //         merchantId: "5744544468085818220",
        //         return: "http://tothemoonmobile.com",
        //         checksum: ""
        //     }
        // } else {
            this.RequireParams = [
                "amount", "bundle", "checksum", "clientUniqueId", "currency", "merchantId", "merchantSiteId", "return", "sessionToken"
            ]

            if (location.hash.length == 0) {
                location.href = "https://tothemoonmobile.com/?error_payment=empty"
                this.RequestParams = undefined
                return
            }
            this.RequestParams = {}
            const params: string[] = location.hash.substr(1).split('&')
            for (const param of params) {
                const paramKey: string = param.split('=')[0];
                (this.RequestParams as any)[paramKey] = param.substr(paramKey.length + 1)
            }

            for (const requireParam of this.RequireParams) {
                if ((requireParam in this.RequestParams) == false) {
                    location.href = ("return" in this.RequestParams ? this.RequestParams.return : "https://tothemoonmobile.com") + "/?error_payment=not_param_" + requireParam
                    this.RequestParams = undefined
                    return
                }
            }

            const newCheckSum = sha256(this.RequestParams.sessionToken + "" + this.RequestParams.amount + this.RequestParams.currency)

            if (newCheckSum != this.RequestParams.checksum) {
                location.href = this.RequestParams.return + "/?error_payment=bad_checksum"
                this.RequestParams = undefined;
                return
            }

            location.hash = ""
        //}

        this.RequestParams.amount += (this.RequestParams.amount?.indexOf(".") == -1 ? ".00" : "")
    }

    private OnErrorText(line: string): void {
        line = line.replace(/_/gi, " ")
        line = line.substr(0, 1).toUpperCase() + line.substr(1)
        this.ErrorCardText = line
    }

    private OnSubmit(): void {
        if (this.AcceptedTerns == false) {
            this.ErrorAcceptedTerns = true
            return
        }
        if (this.CardHolderName.trim().length == 0) {
            this.ErrorCardHolderName = true
            return
        }
        this.HasSubmitted = true;
        this.ErrorCardText = undefined;
        (window as any).DoSubmit(this.CardHolderName)
    }

    private OnFinishSubmit(result: WebSdkPaymentResultType | undefined): void {
        console.log('OnFinishSubmit')
        this.HasSubmitted = false
    }
}
